.hands-container {
	position: relative;
	max-width: 238px;

	&::before {
		content: '';
		display: block;
		position: absolute;
		width: 238px;
		height: 62px;
		top: 0;
		background-image: url(../../images/hands-icon.png);
		background-repeat: no-repeat;
		background-position: center;
	}

	&::after {
		content: '';
		display: block;
		position: absolute;
		width: 37px;
		height: 62px;
		top: 0;
		right: 0;
		background-image: url(../../images/hands-icon-right.png);
		background-repeat: no-repeat;
		background-position: bottom right;
		transform: translate3d(-50px, -15px, 0);
		animation: moveHand 800ms ease-out;
		animation-delay: 500ms;
		animation-fill-mode: forwards;
	}

	@keyframes moveHand {
		0% {
			transform: translate3d(-50px, -15px, 0);
		}

		20% {
			transform: translate3d(-40px, 15px, 0);
		}

		40% {
			transform: translate3d(-30px, -25px, 0);
		}

		60% {
			transform: translate3d(-20px, 20px, 0);
		}

		80% {
			transform: translate3d(-10px, -25px, 0);
		}

		100% {
			transform: translate3d(0, 0, 0);
		}
	}
}

.canvas {
	transition: opacity 1s ease;
}

.apply-text {
	font-size: 3rem;

	@media (min-width: 38rem) {
		font-size: 4rem;
	}
}
