.footer-container {
	& >  {
		flex: 1 1 0;
	}

	.footer-img {
		max-width: 120px;

		&--melanoma {
			max-width: 90px;
		}

		@media (max-width: 38rem) {
			height: auto;
		}
	}

	a {
		text-decoration: none;
		color: #000;
		cursor: pointer;
	}
}

.footer-zigzag {
	&::after {
		content: '';
		display: block;
		width: 100vw;
		height: 37px;
		background-image: url(../../images/chevron-header.jpg);
		background-repeat: repeat-x;
		background-position: center;
		position: absolute;
		bottom: 0;
		left: 0;
	}
}
